	module.exports = {"data":{"markdownRemark":{"html":"<p>懐柔の慕田峪の万里の長城に行くと、万里の長城はより危険な場所だと言われています。普通の人はBadalingに行き、Mutianyuは少なくなります。</p>\n<p>Huairouはかつて貧しい郡であったが、今や経済は発展している。郡は長い地形を持ち、90％は山岳地帯です。山の状況は比較的後退している。万里の長城の近くにいるときは、自然環境が特に良いという深い感覚があり、緑色の植生は緑豊かで連続的で、気分がリフレッシュします。郡党委員会では今年、郡党委員会が1つを押収したと述べた。山岳部の小学校の学生は、社会的支援と政府支援を通じて集中的に学校に通っていた。教育を掴むことは基本的な出来事です。</p>\n<p>Mutianyu万里の長城に行き、ケーブルカーを持ち上げてください。山は旗で飛んでいて、古代の兵士たちが身に着けているような服を着た農家もいます。霧が遠い場面を見るのが難しいのは残念です。そうでなければ、それはとても楽しいものでなければなりません。それはちょうど \"万里の長城の内部と外部、唯一のゆうゆう\"のようなものです。</p>\n<p>洪宗寺には祖先がいたと言われていますが、大寺院に比べ、紅楼寺はあまり有名ではありません。古くからの近代中国と外国が結びつき、改革と開放が決して忘れられていない。 「古いことを忘れてはいないが、何か新しいことはできない。人々を改革するのは良いことではない。馬山魯迅とは違うのではないか」と誰もが笑って笑っている。</p>\n<p>修道士も開いています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0144.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0144.md","frontmatter":{"title":"9月3日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0144.md absPath of file >>> MarkdownRemark"}}

